import http from 'src/utils/http';

export function create({categoryGuid, params}) {
  return http.post(`/technical/category/${categoryGuid}/assignedEntities`, params);
}

export function remove(param) {
  return http.delete(`/technical/category/relation`, { data: param });
}

export function getRelations(categoryGuid, param) {
  return http.post(`/technical/category/relations/${categoryGuid}`, param);
}

export function findRelations(param) {
  return http.post(`/technical/table/relations`, param);
}

export function getOrganization({param, pid}) {
  return http.post(`/technical/organization/${pid}`, param);
}

export function queryOrganizations(params, pid) {
  return http.post(`/technical/organization`, params);
}

export function setOwners(param) {
  return http.post(`/technical/owner/table`, param);
}

export function getLastestOrga() {
  return http.put('/technical/organization');
}



