import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import * as service from 'src/service/business-obj/column';

const {
  GET_ALL_BUSINESS,
  GET_ALL_BUSINESS_SUCCESS,
  GET_ALL_BUSINESS_FAILURE,

  GET_BUSINESS_TABLE,
  GET_BUSINESS_TABLE_SUCCESS,
  GET_BUSINESS_TABLE_FAILURE,

  GET_BUSINESS_TABLE_COLUMNS,
  GET_BUSINESS_TABLE_COLUMNS_SUCCESS,
  GET_BUSINESS_TABLE_COLUMNS_FAILURE,

  UPDATE_TABLE_NAME,
  UPDATE_TABLE_NAME_SUCCESS,
  UPDATE_TABLE_NAME_FAILURE,

  UPDATE_COLUMN_NAME,
  UPDATE_COLUMN_NAME_SUCCESS,
  UPDATE_COLUMN_NAME_FAILURE,

  CHECK_COLUMN_EXIST,
  CHECK_COLUMN_EXIST_SUCCESS,
  CHECK_COLUMN_EXIST_FAILURE,

  UPDATE_COLUMN_NAME_BATCH,
  UPDATE_COLUMN_NAME_BATCH_SUCCESS,
  UPDATE_COLUMN_NAME_BATCH_FAILURE
} = types;

// initial state
const state = {
  businesses: {
    status: SUCCESS,
    list: [],
    total: 0
  },
  tables: {
    status: SUCCESS,
    list: []
  },
  columns: {
    status: SUCCESS,
    list: [],
    total: 0
  },
  columnsBatch: {
    status: SUCCESS,
    list: [],
    total: 0
  },
  updateTableNameStatus: SUCCESS,
  updateColumnNameStatus: SUCCESS,
  checkExistStatus: SUCCESS
};

const actions = {

  async getBusiness(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_ALL_BUSINESS, params);
      const res = await service.getBusiness(params);
      commit(GET_ALL_BUSINESS_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_ALL_BUSINESS_FAILURE, params);
      throw err;
    }
  },

  async getBusinessTable(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_BUSINESS_TABLE, params);
      const res = await service.getBusinessTable(params.id, params.param);
      commit(GET_BUSINESS_TABLE_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_BUSINESS_TABLE_FAILURE, params);
      throw err;
    }
  },

  async getColumns(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_BUSINESS_TABLE_COLUMNS, params);
      const res = await service.getColumns(params.id, params.param, params.order);
      commit(GET_BUSINESS_TABLE_COLUMNS_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_BUSINESS_TABLE_COLUMNS_FAILURE, params);
      throw err;
    }
  },

  async exportModule(context, params) {
    try {
      const res = await service.exportModule(params.id);
      return res;
    } catch (err) {
      throw err;
    }
  },

  async updateTableName(context, params) {
    const {
      commit
    } = context;

    try {
      commit(UPDATE_TABLE_NAME, params);
      const res = await service.updateTableName(params);
      commit(UPDATE_TABLE_NAME_SUCCESS, res);
      return res;
    } catch (err) {
      commit(UPDATE_TABLE_NAME_FAILURE, params);
      throw err;
    }
  },

  async updateColumnName(context, params) {
    const {
      commit
    } = context;

    try {
      commit(UPDATE_COLUMN_NAME, params);
      const res = await service.updateColumnName(params.id, params.param);
      commit(UPDATE_COLUMN_NAME_SUCCESS, res);
      return res;
    } catch (err) {
      commit(UPDATE_COLUMN_NAME_FAILURE, params);
      throw err;
    }
  },

  async updateColumnNameBatch(context, res) {
    const {
      commit
    } = context;

    try {
      commit(UPDATE_COLUMN_NAME_BATCH);
      commit(UPDATE_COLUMN_NAME_BATCH_SUCCESS, res);
      return res;
    } catch (err) {
      commit(UPDATE_COLUMN_NAME_BATCH_FAILURE, res);
      throw err;
    }
  },

  async checkExist(context, params) {
    const {
      commit
    } = context;

    try {
      commit(CHECK_COLUMN_EXIST, params);
      const res = await service.checkExist(params.id, params.param);
      commit(CHECK_COLUMN_EXIST_SUCCESS, res);
      return res;
    } catch (err) {
      commit(CHECK_COLUMN_EXIST_FAILURE, params);
      throw err;
    }
  }
};

const mutations = {
  [GET_ALL_BUSINESS](state, params) {
    state.businesses = {
      ...state.businesses,
      status: LOADING
    };
  },
  [GET_ALL_BUSINESS_SUCCESS](state, payload) {
    state.businesses = {
      list: payload.lists,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [GET_ALL_BUSINESS_FAILURE](state) {
    state.businesses = {
      ...state.businesses,
      status: FAIL
    };
  },

  [GET_BUSINESS_TABLE](state, params) {
    state.tables = {
      ...state.tables,
      status: LOADING
    };
  },
  [GET_BUSINESS_TABLE_SUCCESS](state, payload) {
    state.tables = {
      list: payload.lists,
      status: SUCCESS
    };
  },
  [GET_BUSINESS_TABLE_FAILURE](state) {
    state.tables = {
      ...state.tables,
      status: FAIL
    };
  },

  [GET_BUSINESS_TABLE_COLUMNS](state, params) {
    state.columns = {
      ...state.columns,
      status: LOADING
    };
  },
  [GET_BUSINESS_TABLE_COLUMNS_SUCCESS](state, payload) {
    state.columns = {
      list: payload.lists,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [GET_BUSINESS_TABLE_COLUMNS_FAILURE](state) {
    state.columns = {
      ...state.columns,
      status: FAIL
    };
  },

  [UPDATE_COLUMN_NAME_BATCH](state) {
    state.columnsBatch = {
      status: SUCCESS,
      list: [],
      total: 0
    };
  },
  [UPDATE_COLUMN_NAME_BATCH_SUCCESS](state, payload) {
    state.columnsBatch = {
      list: payload.lists,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [UPDATE_COLUMN_NAME_BATCH_FAILURE](state) {
    state.columnsBatch = {
      status: SUCCESS,
      list: [],
      total: 0
    };
  },

  [UPDATE_TABLE_NAME](state, params) {
    state.updateTableNameStatus = LOADING;
  },
  [UPDATE_TABLE_NAME_SUCCESS](state, payload) {
    state.updateTableNameStatus = SUCCESS;
  },
  [UPDATE_TABLE_NAME_FAILURE](state) {
    state.updateTableNameStatus = FAIL;
  },

  [UPDATE_COLUMN_NAME](state, params) {
    state.updateColumnNameStatus = LOADING;
  },
  [UPDATE_COLUMN_NAME_SUCCESS](state, payload) {
    state.updateColumnNameStatus = SUCCESS;
  },
  [UPDATE_COLUMN_NAME_FAILURE](state) {
    state.updateColumnNameStatus = FAIL;
  },

  [CHECK_COLUMN_EXIST](state, params) {
    state.checkExistStatus = LOADING;
  },
  [CHECK_COLUMN_EXIST_SUCCESS](state, payload) {
    state.checkExistStatus = SUCCESS;
  },
  [CHECK_COLUMN_EXIST_FAILURE](state) {
    state.checkExistStatus = FAIL;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



