import http from 'src/utils/http';

export function getBusiness(params) {
  return http.post('/businesses/relations', params);
}

export function getBusinessTable(id, params) {
  return http.post(`/businesses/${id}/tables`, params);
}

export function getColumns(id, params, order) {
  if (order.sort && order.sortAttribute) {
    return http.post(`/businesses/table/${id}/columns?sort=${order.sort}&sortAttribute=${order.sortAttribute}`, params);
  } else {
    return http.post(`/businesses/table/${id}/columns`, params);
  }
}

export function updateTableName(params) {
  return http.put('/businesses/table', params);
}

export function updateColumnName(id, params) {
  return http.put(`/businesses/table/${id}/columns`, params);
}

export function exportModule(id) {
  // return http.get(`/businesses/excel/${id}/template`);
  return http.download({
    action: `/businesses/excel/${id}/template`
  }, {
    filePath: `/businesses/excel/${id}/template`
  });
}

export function checkExist(id, params) {
  return http.post(`/businesses/table/${id}/columns/check`, params);
}



